export function innerHTMLCharacter(method, obj) {
    //     enclosed-li ( )
    if (method === 'firstName' || method === 'secondName') {
        if (method === 'secondName') {
            return obj.firstName + ' ' + '<b>' + obj.secondName + '</b>';
        }

        return '<b>' + obj.firstName + '</b> ' + obj.secondName;
    }
}


